body {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#root {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}
.mainHeader {
    padding-top: 30px;
    padding-bottom: 60px;
    background-image: url(/clicky-game/static/media/controller.e6052918.jpg);
    background-repeat: no-repeat;
    background-size: 300px;
    background-position: left;
    background-color: #bdcdcd;
}

@media only screen and (max-width: 992px) {
    .mainHeader {
        background-position: top;
    }
}
.reactCard {
    cursor: pointer;
}

.reactCardImage {
    height: 225px;
}
.vgmNavLogo {
    height: 30px;
    padding-right: 10px;
  }
  
  .nav-wrapper {
    padding-right: 20px;
    padding-left: 20px;
  }
  
  nav .nav-wrapper {
    height: unset;
    font-size: 25px;
  }

  @media only screen and (max-width: 600px) {
    .myNavItem {
      text-align: center;
    }
  }
.incorrectToast {
  background-color: red;
}

.wonToast {
  background-color: green;
}

.correctToast {
  background-color: blue;
}

.toast {
  font-size: 30px;
}

@media only screen and (max-width: 600px) {
  .mainHeader {
    margin-top: 15px;
  }
}
